@import url(https://fonts.googleapis.com/css2?family=Nunito&display=swap);
body {
  margin: 0;
  font-family: 'Nunito', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: rgb(32, 32, 32);
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*,::before,::after{box-sizing:border-box}body{height:100vh;display:flex;justify-content:center;text-align:center;font-family:'Nunito', sans-serif}body h1{color:#ddd}body button{margin-top:50px;outline:none;border:none;padding:8px 16px;font-size:15px;box-shadow:0 10px 10px #4d4d4d;transition:box-shadow 500ms;border-radius:7px;background:#7a9cf1;-webkit-user-select:none;-ms-user-select:none;user-select:none;-webkit-appearance:none;appearance:none;user-select:none;min-width:90px}body button:hover{box-shadow:0 10px 10px -5px #4d4d4d}body button.on{transition:background-color 300ms;background-color:#29a139}body button.off{transition:background-color 300ms;background-color:#ee4848}body .light{margin-top:120px;height:150px;width:150px;border-radius:50%}body .light.off{background-color:#797979;box-shadow:inset #a1a1a1 0 -1px 9px, #cac9c9 0 2px 12px;transition:all .15s}body .light.on{-webkit-transform:scale(102%);transform:scale(102%);background-color:#ffff48;box-shadow:0 0 35px 40px #ffff62,inset #d4d40f 0 -1px 5px;-webkit-animation:light .15s 1;animation:light .15s 1}@-webkit-keyframes light{from{background-color:#797979;box-shadow:none;-webkit-transform:scale(100%);transform:scale(100%)}50%{background-color:#ffff48;box-shadow:0 0 16px 20px #ffff62,inset #e9e91e 0 -1px 9px;-webkit-transform:scale(101%);transform:scale(101%)}to{background-color:#ffff48;box-shadow:0 0 35px 40px #ffff62,inset #e7e70f 0 -1px 9px;-webkit-transform:scale(102%);transform:scale(102%)}}@keyframes light{from{background-color:#797979;box-shadow:none;-webkit-transform:scale(100%);transform:scale(100%)}50%{background-color:#ffff48;box-shadow:0 0 16px 20px #ffff62,inset #e9e91e 0 -1px 9px;-webkit-transform:scale(101%);transform:scale(101%)}to{background-color:#ffff48;box-shadow:0 0 35px 40px #ffff62,inset #e7e70f 0 -1px 9px;-webkit-transform:scale(102%);transform:scale(102%)}}

